@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;500&display=swap);

body {
  margin: 0;
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f8f9fa;
}

code {
  font-family:
    source-code-pro,
    Menlo,
    Monaco,
    Consolas,
    'Courier New',
    monospace;
}

:root{--black: #000;--white: #fff;--btn-red: #ff385c}*{margin:0;padding:0;box-sizing:border-box;font-family:'Quicksand', sans-serif}.container{margin:0 auto;width:90%}.back{color:#041515;font-size:1.4rem;margin:3rem 0}#arrow{width:1.4rem}img{width:100%}.m-1{margin-top:3rem;margin-bottom:3rem}.capsule{background:#dfe0e1;border-radius:0.6rem;width:-webkit-max-content;width:-moz-max-content;width:max-content;padding:0.35rem 0.8rem;margin-right:1rem}.align-center{align-items:center;justify-content:space-between}.center{margin:3rem auto;width:20rem;display:block}li{list-style:none}a{text-decoration:none}.d-flex{display:flex}.d-grid{display:grid}h3{font-size:2rem}nav{padding:1.5rem 0;background:#000;background:var(--black);height:120px}nav h1{color:#fff;color:var(--white);font-size:3rem}nav li a{color:#fff;color:var(--white);font-size:1.5rem;background:#ff385c;padding:1rem 3.5rem;border-radius:2rem}h2{font-size:3rem}select{width:25rem;font-size:1.3rem;padding:1rem 1.5rem;cursor:pointer;border:0;outline:0;box-shadow:2px 2px 8px #999}select option{font-size:1.3rem;border-radius:1.5rem}.tags{align-items:center}.tags p{background:none;border:1px solid #59626c;color:#59626c}.single-details{grid-template-columns:3fr 1fr;grid-gap:4rem;margin-top:3rem}.single-details p{margin:1.5rem 0}.card-container{width:100%;height:auto;border-radius:1.8rem;background:#fff;background:var(--white);grid-template-columns:2fr 3fr 1fr;grid-gap:2rem;padding:3rem 2.5rem;margin:1.5rem 0;box-shadow:0 4px 8px 0 rgba(0,0,0,0.15),0 6px 20px 0 rgba(0,0,0,0.1)}.card-container .job-details{grid-template-columns:1fr 4fr;grid-gap:1rem}.card-container .job-details .company-logo{height:50px;margin:2rem 0}.card-container .job-details .more-info p li{font-size:1.3rem}.card-container .job-details .more-info h2{font-size:3rem}.card-container .job-details .company-info *{margin-bottom:1rem}.card-container .job-details .company-info .job-title{font-size:1.5rem;font-weight:400}.card-container .job-details .company-info p{font-size:1.2rem}.card-container .job-details .company-info .location{background:#dfe0e1}.card-container .last-details{align-items:center}.card-container .job-tag{background:#59626c;color:#fff;color:var(--white)}.single-job-title{font-size:3rem}#apply-btn{background:blue;color:#fff;color:var(--white);width:100%;padding:1.5rem;border-radius:0.7rem;border:0;display:block;text-align:center}.uppercase{text-transform:uppercase}

